import React from 'react';
import Navbar from './Navbar';

const Header = () => {
  const styles = {
    header: {
      display: "flex",
      justifyContent: "space-between",
      padding: "15px",
      backgroundColor: "royalblue",
      color: "#fff"
    }
  }

  return (
    <header style={styles.header}>
      <div className="logo">
        React Animation
      </div>

      <Navbar />
    </header>
  );
};

export default Header;